import React from 'react';
import { Progress } from "reactstrap";
import './skills.css';

export default function Skills() {
    return (
			<div>
				<section id="skills" className="skills section-bg">
					<div className="container" data-aos="fade-up">
						<div className="section-title">
							<h2>Skills</h2>
							<p>
								Full Stack Web Developer| Quick Learner | Analytically apt | C++
							</p>
						</div>

						<div className="row skills-content">
							<div className="col-lg-6">
								<div className="progress">
									<span className="skill">
										React <i className="val">75%</i>
									</span>
									<div className="progress-bar-wrap">
										<Progress animated value="70" />
									</div>
								</div>

								<div className="progress">
									<span className="skill">
										MongoDB <i className="val">62%</i>
									</span>
									<div className="progress-bar-wrap">
										<Progress animated value="62" />
									</div>
								</div>

								<div className="progress">
									<span className="skill">
										Express <i className="val">61%</i>
									</span>
									<div className="progress-bar-wrap">
										<Progress animated value="61" />
									</div>
								</div>
							</div>

							<div className="col-lg-6">
								<div className="progress">
									<span className="skill">
										DSA <i className="val">70%</i>
									</span>
									<div className="progress-bar-wrap">
										<Progress animated value="70" />
									</div>
								</div>

								<div className="progress">
									<span className="skill">
										Java Script<i className="val">64%</i>
									</span>
									<div className="progress-bar-wrap">
										<Progress animated value="64" />
									</div>
								</div>

								<div className="progress">
									<span className="skill">
										HTML/CSS <i className="val">65%</i>
									</span>
									<div className="progress-bar-wrap">
										<Progress animated value="65" />
									</div>
								</div>
							</div>
						</div>
					</div>
				</section>
			</div>
		);
}
