import React, { Component } from "react";
import AOS from'aos';
import "aos/dist/aos.css";
import "./front.css";
import {
	Link,
	Element,
	Events,
	animateScroll as scroll,
	scrollSpy,
	scroller
} from "react-scroll";

export default class Front extends Component {
	componentDidMount() {
		Events.scrollEvent.register("begin", function (to, element) {
			console.log("begin", arguments);
		});

		Events.scrollEvent.register("end", function (to, element) {
			console.log("end", arguments);
		});

		scrollSpy.update();
		AOS.init({
			// initialise with other settings
			duration: 1500,
			once: true,
		});
	}
	scrollApp() {
		scroller.scrollTo("App", {
			smooth: true,
		});
	}
	scrollAbout() {
		scroller.scrollTo("About", {
			smooth: true,
		});
	}
	scrollSkills() {
		scroller.scrollTo("Skills", {
			smooth: true,
		});
	}
	scrollProjects() {
		scroller.scrollTo("Projects", {
			smooth: true,
		});
	}
	scrollContactus() {
		scroller.scrollTo("Contactus", {
			smooth: true,
		});
	}
	render() {
		return (
			<div>
				<button type="button" className="mobile-nav-toggle d-xl-none">
					<i style={{ color: "#f1d4d4" }} className="fa fa-bars"></i>
				</button>

				<header
					id="header"
					className="d-flex flex-column justify-content-center"
				>
					<nav className="nav-menu">
						<ul>
							<li className="active" data-aos="fade-right">
								<a href="#hero" onClick={() => this.scrollApp()}>
									<i className="fa fa-home"></i> <span>Home</span>
								</a>
							</li>
							<li data-aos="fade-right" data-aos-delay="100">
								<a href="#about" onClick={() => this.scrollAbout()}>
									<i className="fa fa-address-card"></i> <span>About</span>
								</a>
							</li>

							<li data-aos="fade-right" data-aos-delay="200">
								<a href="#skills" onClick={() => this.scrollSkills()}>
									<i className="fa fa-tasks"></i>
									<span>Skills</span>
								</a>
							</li>
							<li data-aos="fade-right" data-aos-delay="300">
								<a href="#projects" onClick={() => this.scrollProjects()}>
									<i className="fa fa-briefcase"></i> <span>Projects</span>
								</a>
							</li>
							<li data-aos="fade-right" data-aos-delay="400">
								<a href="#contact" onClick={() => this.scrollContactus()}>
									<i className="fa fa-envelope-open"></i> <span>Contact</span>
								</a>
							</li>
						</ul>
					</nav>
				</header>

				<section
					id="hero"
					className="d-flex flex-column justify-content-center"
				>
					<div className="container">
						<h1 data-aos="zoom-in" data-aos-delay="100">
							Rakshit Jain
						</h1>
						<p data-aos="zoom-in" data-aos-delay="200">
							I'm{" "}
							<span
								className="typed"
								data-typed-items="MERN Stack developer,Competitive Programmer"
							></span>
						</p>
						<div
							className="social-links"
							data-aos="zoom-in"
							data-aos-delay="300"
						>
							<a
								href="https://www.facebook.com/rakshit.dhoka/"
								className="facebook"
							>
								<i className="fa fa-facebook"></i>
							</a>
							<a
								href="https://www.instagram.com/rakshitjain13/"
								target="_blank"
								className="instagram"
							>
								<i className="fa fa-instagram"></i>
							</a>
							<a
								href="https://github.com/rakshitjain13"
								target="_blank"
								className="fa fa-github"
							>
								<i className="bx bxl-skype"></i>
							</a>
							<a
								href="https://www.linkedin.com/in/rakshit-jain-9b83b5170/"
								target="_blank"
								className="fa fa-linkedin"
							>
								<i className="bx bxl-linkedin"></i>
							</a>
							<a href="mailto:rakshitjain19@cse.iiitp.ac.in" className="">
								<i className="fa  fa-paper-plane"></i>
							</a>
						</div>
					</div>
				</section>
			</div>
		);
	}
}
